


















































































































































import axios from 'axios'
import { Component, Vue } from 'vue-property-decorator'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import AppFooter from '../../components/layout/AppFooter.vue'

@Component({
  name: 'Home',
  components: {
    FontAwesomeIcon,
    AppFooter
  }
})
export default class Home extends Vue {
  // randomuser.me data object
  randomUsers: any = false
  examplePosts: object = [
    { img: '/static/img/home/coffee-640-360.png', time: '1.2 km - Fri, 4pm - 7pm', icon: 'coffee', text: 'Just got some Blue Mountain Coffee, you need to come try this!! <a>#coffee</a> <a>#coffeelover</a>' },
    { img: '/static/img/home/basketball-640-360.png', time: '2.4 km - Sat, 11am - 4pm', icon: 'basketball-ball', text: 'Making the most of this weather by shooting some hoops out by the beach. 🙌 <a>#basketball</a>' },
    { img: '/static/img/home/cocktails-640-360.png', time: '400m - Fri, 9pm - 2am', icon: 'glass-martini', text: 'TGIF! Celebrating the end of the week by staying out all night! <a>#cocktails</a> <a>#party</a>' }
  ]

  mounted () {
    axios.get(`https://randomuser.me/api?results=3`)
    .then(response => {
      // JSON responses are automatically parsed.
      this.randomUsers = response.data.results
    })
    .catch(e => {
      this.$toast.open({
        duration: 3500,
        message: `Unable to fetch sample data!`,
        position: 'is-top',
        type: 'is-danger'
      })
    })
  }

  getRandomUserName (index: number): string {
    let fn = this.randomUsers[index].name.first.charAt(0).toUpperCase() + this.randomUsers[index].name.first.substr(1)
    let ln = this.randomUsers[index].name.last.charAt(0).toUpperCase() + this.randomUsers[index].name.last.substr(1)
    return fn + ' ' + ln
  }

  getRandomUserPicture (index: number): string {
    return this.randomUsers[index].picture.thumbnail
  }
}



